@import '../../../styles/sass/variables.scss';
@import '../../../styles/sass/mixins.scss';

.mypost{
    display: grid;
    grid-template-rows: 2fr 5fr;
    .mypostContainer{
        display: grid;
        grid-template-columns: 1fr 5fr;
        margin: 0;
        border-radius: 5px;
        background-color: $blokColor;
        align-content: space-between;
        justify-content: center;
        justify-items: center;
        align-items: center;
        .ava{
            padding: 5px;
            width: 70px;
            height: 70px;
            border-radius: 50%;
        }
        .textarea{
            background-color: $inputColor;
            width: 100vh; 
            height: 80px;
            margin: 5px;
            border-radius: 5px;
            padding: 10px;
            resize: none;
        }
        & button{
            border: medium none;
            @include text( 13px, 500, 1.5);
            padding: 4px 20px;
            border-radius: 4px;
            background-color:$primaryColor;
        }
    }
    .myPosts{
        display: grid;
        justify-items: center;
        @include text( 16px, 500, 1.5);
        background-color: $blokColor;
        border-radius: 5px;
        margin: 10px 0;
    }
}










